
<script>
import LeadDataService from "../../../services/DataService";
import DynamicForm from "../../../components/Common/DynamicForm.vue";
import modalpricebook from '../../../components/Common/Modal/modalpricebook.vue'
var ajaxRequestComplete = true;
export default {
  components: {
        DynamicForm,
        modalpricebook
    },
    data() {
        return {
                isLoading:false,
                value: "",
                LeadData: null,
                LeadOwner: null,
                GroupBy: [],
                Channel: null,
                IndustryType: null,
                Status: null,
               salesorderID: this.$route.params.id, 
                options: {
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    maxDate: new Date(Date.now())
                },
                DependentOption: [{ 
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    minDate: new Date(Date.now())
                }
                ],
                selected: false,
                selectoptions: [],
                Address: null,
                ProductSummary: null,
                CountryData: [],
                AccountData: [],
                ShippingContactPerson: [],
                showPricebookModal: false,
                selectedItemIndex: null,
                AccountId: 0,
                statusId: 0,
                submited: false,
                isTaxNameFilled: false,
                vSelectValidate: false,
                vSelectContactPerson: false,
                checkForEdit: false,
                qlist: '',
                redirectURL: '',
                sameShippingAddressAsBilling: false,
                Ischeckshipping: false,
                IsInventory: true,  
                dateformat:null, 
                userId:null,
                companyId:null,
                isProductSelectLoading:false             
            }
    },
    created: function () {
            this.userId= this.GetUserInfo.ID;
            this.companyId= this.GetUserInfo.CompanyId;
            this.dateformat= this.GetUserInfo.DateFormat;
            Number.prototype.toFixed2 = function () {
            var amount = Math.floor(parseFloat(this, 10) * 100) / 100;
            return parseFloat(amount, 10).toFixed(2);
        };
            this.FetchData();
            this.GetCountryData('', 'COUNTRIES');
            this.qlist =   this.$route.params.reqfrom,
            this.redirectURL = (decodeURIComponent(this.qlist)).split("&amp;").join("&");
    },
    // directives: {
    //         'click-outside': {
    //             bind: function (el, binding, vNode) {
                    
    //                 if (typeof binding.value !== 'function') {
    //                     const compName = vNode.context.name
    //                     let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
    //                     if (compName) { warn += `Found in component '${compName}'` }
    //                 }
                 
    //                 const bubble = binding.modifiers.bubble
    //                 const handler = (e) => {
    //                     if (bubble || (!el.contains(e.target) && el !== e.target)) {
    //                         binding.value(e)
    //                     }
    //                 }
    //                 el.__vueClickOutside__ = handler
              
    //                 document.addEventListener('click', handler)
    //             },
    //             unbind: function (el, binding) {
                 
    //                 document.removeEventListener('click', el.__vueClickOutside__)
    //                 el.__vueClickOutside__ = null
    //             }
    //         }
    // },
    methods: {
        getQuantityValidationRule(item){
            var initialRule={}
            initialRule.required=(item.ProductName != '' && item.ProductName != null);
            initialRule.numeric=(item.ProductName != '' && item.ProductName != null);
            initialRule.min_value=(item.ProductName != '' && item.ProductName != null)?0:0;
                initialRule.max_value=(item.ProductName != '' && item.ProductName != null)?item.ProductInStock:0;
            return initialRule;
        },
        onCancel: function () {
            if(this.$route.params.reqFromId!="0"){
                this.$router.push({path:`/CRM/ViewDealDetails/${this.$route.params.reqFromId}/CRM/CRM_DEALS`});
            }else{
                this.$router.push({ name: 'CRMSalesOrderListing' });
            }
        },
        checkForSameBillingAndShippingAddress: function () {
            if (this.Address.ShippingAddress.ShippingAccountAddress == this.Address.BillingAddress.BillingAccountAddress &&
                this.Address.ShippingAddress.ShippingAccountAddress2 == this.Address.BillingAddress.BillingAccountAddress2 &&
                this.Address.ShippingAddress.ShippingCity == this.Address.BillingAddress.BillingCity &&
                this.Address.ShippingAddress.ShippingZipCode == this.Address.BillingAddress.BillingZipCode &&
                this.Address.ShippingAddress.ShippingState == this.Address.BillingAddress.BillingState &&
                this.Address.ShippingAddress.ShippingAccountCity == this.Address.BillingAddress.BillingAccountCity &&
                this.Address.ShippingAddress.ShippingCountry == this.Address.BillingAddress.BillingCountry) {
                this.sameShippingAddressAsBilling = true;
                this.Ischeckshipping = true;
            }
        },
        chkapplicableshipping: function (e) {
            this.Ischeckshipping=$(e.target).prop("checked");
            if (!this.Ischeckshipping) {
                this.Address.ShippingAddress.ShippingAccountCity = '';
                this.Address.ShippingAddress.ShippingAccountAddress = '';
                this.Address.ShippingAddress.ShippingZipCode = '';
                this.Address.ShippingAddress.ShippingAccountAddress2 = '';
                this.Address.ShippingAddress.ShippingState = '';
                this.Address.ShippingAddress.ShippingContactPerson = '';
                this.sameShippingAddressAsBilling = false;
            }
        },
        clearRateIfNameEmpty: function (item, type, isInPercentage) {
            if ($.trim(item.TAX_NAME) == '') {
                item.TAX_RATE = 0;
                item.TAX_NAME = $.trim(item.TAX_NAME)
            } else {
                this.isTaxNameFilled = false;
            }
        },
        outside: function (e) {
            // $.each(this.ProductSummary.SummaryData, function (key, item) {
            //     item.toggleTax = false;
            //     item.toggleDiscount = false;
            // });
        },
        onlyDecimal: function (item, type, isInPercentage) {
            if (type == 'TAX') {
                if (item.TAX_NAME != '') {
                    item.TAX_RATE = item.TAX_RATE.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');
                    if (isInPercentage && parseFloat((item.TAX_RATE != '') ? item.TAX_RATE : 0) > 100) {
                        item.TAX_RATE = '100';
                    }
                } else {
                    this.isTaxNameFilled = true;
                    item.TAX_RATE = "0";
                }
            } else if (type == 'DISCOUNT') {
                item.ProductDiscountValue = item.ProductDiscountValue.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');
                if (isInPercentage && parseFloat((item.ProductDiscountValue != '') ? item.ProductDiscountValue : 0) > 100) {
                    item.ProductDiscountValue = '100';
                }
            }
        },
        DependentDateObject: function (item, date) {
            debugger;
            if (item.name == 'order_date') {
                var checkDate = new Date();
                if ((typeof date !== 'undefined') && date != '') {
                    checkDate = new Date(Date.parse(date));
                }
                else {
                    checkDate = item.value;
                }
                this.DependentOption[0] = {
                  
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    minDate: checkDate
                }
                if (this.ajaxRequestComplete) {
                    $.each(this.GroupBy, function (filedIndex, fieldItem) {
                        $.each(fieldItem.Data, function (index, element) {
                            if (element.name == "order_expiry_date") {
                                element.value = '';
                            }
                        });
                    });
                }
            }
        },
        onKeyDown: function () {
            ajaxRequestComplete = true;
        },
        onSearch: function onSearch(search, objItem, isFor) {      
            if(search)          
            this.searchSelectData(search, this, objItem, isFor);
        },
        searchSelectData:function (search, vm, objItem, isFor) {
            this.ShowSearchData(search, vm, objItem, isFor);
        },
        ShowSearchData: function (search, vm, objItem, isFor) {
            vm.isProductSelectLoading=true;
            var url = "";
            if (isFor == "ITEM_NAME_SUMMARY") {
                url =  `id=&moduleName=${vm.$route.params.moduleName}&type=ITEM_NAME`;                  
            }
            else {
                var refId = 0;
                if (vm.AccountId > 0 && objItem.field_code != "ACCOUNTS") {
                    refId = vm.AccountId;
                }
                url =`id=&moduleName=${vm.$route.params.moduleName}&type=${objItem.field_code}&refId=${refId}`;                 
            }
            LeadDataService.getDDLData(url + "&search=" + escape(search))
                .then(response => {
                    vm.isProductSelectLoading=false;
                    var json = [];
                    if (response.data != null && response.data!='') {
                        json = response.data;
                    } 

                    if (isFor == "ITEM_NAME_SUMMARY") {
                        $.each(vm.ProductSummary.SummaryData, function (key, item) {
                            var prodctValue = "";
                            if (item.ProductName != null && Array.isArray(item.ProductName)) {
                                prodctValue = item.ProductName[0].value;
                            }
                            else if (item.ProductName != null && !Array.isArray(item.ProductName)) {
                                prodctValue =item.ProductName;
                            }
                            json.DATA = json.DATA.filter(function (el) {
                                return (item.ProductName != null && el.value == prodctValue) ? false : true;
                            });
                        });
                         objItem.ProductList = json.DATA;
                    }
                    else {
                         objItem.select_options = json.DATA;
                    }
                });
        },
        setSelectedValue: function (item, val, isFor, type) {
            var vm=this;
            // vm.$refs.form.setFieldTouched(item.name, false); 
            if (isFor == 'shipping') {
                var refId = val;
                if (isFor != 'search') {
                    refId = val.value;
                }
                var search = '';
                if (typeof (refId) != "undefined") {
                    var url =`id=&moduleName=${vm.$route.params.moduleName}&type=${type}&refId=${refId}`;                
                    LeadDataService.getDDLData(url + "&search=" + escape(search))
                        .then(response => {                         
                            var json = response.data;
                            if (json != null) {
                                vm.Address.ShippingAddress.ShippingContactPerson = '';
                                return vm.ShippingContactPerson = json.DATA;
                            }
                        });
                }
            }
            else if (isFor == "BillingContactPerson") {              
                item.BillingContactPerson = val?val:item.BillingContactPerson;                
            }
            else if (isFor == "ShippingContactPerson") {
                item.ShippingContactPerson = val?val:item.ShippingContactPerson;               
            }
            else if (isFor == "ITEM_NAME_SUMMARY") {
                item.ProductName = val;
                if (val == null) {
                    item.ProductQuantity = "0";
                    item.ProductAmount = "0.00";
                    item.ProductDiscount = "0.00";
                    item.ProductTax = "0.00";
                    item.ProductRate = "0.00";
                    item.ProductItemTotal = "0.00";
                }
                if (ajaxRequestComplete) {
                    if (val.first_value > 0) {
                        item.ProductInStock = val.first_value;
                    }
                    if (val.second_value > 0) {
                        item.ProductRate = val.second_value;
                    }
                    vm.ShowTaxItems(item);
                    vm.CalculateAmount(item);
                    item.PriceBookId = 0;
                }
            }
            else {
                try {
                    var selectedVal=val;
                    if (Array.isArray(val)) {
                        selectedVal= (typeof val[0] === 'object' && val[0] !== null) ? val[0].value : val[0];
                    }
                    else {
                       
                        selectedVal =val.value;
                    }
                    if (item.name == 'account_id' &&selectedVal> 0) {
                        this.AccountId = selectedVal;
                        this.FillDropdown('ACCOUNT_CONTACTS', this.AccountId);
                        this.FillDropdown('ACCOUNT_DEALS', this.AccountId);
                    }
                }
                catch (ex) {
                }
            }
        },      
        SubmitData: function ( valid,errors,statusType) {
                var chkSummaryData = "";
                var vm=this;           
            vm.$refs.form.validate().then((result) => {
                vm.vSelectValidate = true;          
                if (result) {  
                    vm.isLoading=true;
                        vm.submited = true;                        
                        var fullJson = vm.LeadData;
                        var data = [];
                        var validation = [];
                        $.each(fullJson, function (key, item) {
                            var validationObject = {
                                field_name: item.name,
                                required: item.is_required,
                                length: item.length,
                                type: item.validation_type
                            };                          
                            var fieldValue = (item.name == "order_id" ? vm.salesorderID : ((typeof item.value === "undefined") ? '' :item.value));
                        fieldValue = typeof fieldValue === "undefined" ? '' : fieldValue;
                            if (item.dt_code == "date") {
                                fieldValue = item.value;
                            }
                            if (item.dt_code == "checkbox") {
                                var filtered = item.select_value.filter(function (el) {
                                    return (el != null && el != "");
                                });
                                fieldValue = filtered.join(',');
                            }
                            if (item.dt_code == "select") {
                                try {
                                    fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue
                                } catch (ex) { }
                            }
                            if(item.name=='created_by'){
                              fieldValue = vm.userId;
                            }
                            if(item.name=='modified_by'){
                              fieldValue = vm.userId;
                            }
                            if(item.name=='company_id'){
                              fieldValue = vm.companyId;
                            }
                            var dataObject = {
                                field_name: item.name,
                                field_value: fieldValue,
                                custom_field_id: item.custom_field_id
                            };
                            data.push(dataObject);
                            validation.push(validationObject);
                        });
                        console.log(data);
                        var productSummaryData = [];
                        $.each(vm.ProductSummary.SummaryData, function (key, item) {

                            var projectId = 0;
                            if (Array.isArray(item.ProductName)) {
                                try {
                                    projectId = (typeof item.ProductName[0] === 'object' && item.ProductName[0] !== null) ? item.ProductName[0].value : item.ProductName[0];
                                } catch (ex) {
                                    projectId = item.ProductName[0];
                                }
                            }
                            else {
                                projectId = item.ProductName.value;
                            }
                            if (projectId > 0) {
                                var dataObject = {
                                    ProductName: projectId,
                                    ProductDescription: item.ProductDescription,
                                    ProductQuantity: item.ProductQuantity,
                                    ProductRate: item.ProductRate,
                                    ProductTax: item.ProductTax,
                                    ProductAmount: item.ProductAmount,
                                    ProductDiscount: item.ProductDiscount,
                                    ProductDiscountType: item.ProductDiscountType,
                                    ProductDiscountValue: ($.trim(item.ProductDiscountValue) != '') ? item.ProductDiscountValue : 0.00,
                                    ProductItemTotal: item.ProductItemTotal,
                                    PriceBookId: item.PriceBookId,
                                    IsTaxApplicable: (item.TaxData != null && item.TaxData.length > 0 ? 1 : 0),
                                    TaxData: item.TaxData,
                                };
                                productSummaryData.push(dataObject);
                            }
                        });
                        if (productSummaryData.length == 0) {
                            chkSummaryData = "no";
                        }
                        var productSummary = {
                            SubTotal: vm.ProductSummary.SubTotal,
                            ShippingCharges: (vm.ProductSummary.ShippingCharges != '') ? vm.ProductSummary.ShippingCharges : '0',
                            Adjustments: (vm.ProductSummary.Adjustments != '') ? vm.ProductSummary.Adjustments : '0',
                            SummaryTotal: vm.ProductSummary.SummaryTotal,
                            SummaryData: productSummaryData
                        }
                       
                        if (vm.ProductSummary.SummaryTotal < 0) {
                            vm.ShowAlert(vm.$t("Total amount cannot be in minus."),"warning", true, vm.$t("Alert"));
                            vm.submited = false;
                            vm.isLoading=false;
                            return false;
                        }
                        if (vm.ProductSummary.SummaryTotal == 0) {
                        vm.ShowAlert("Total amount cannot be zero.", "warning", true, vm.$t("Alert"));
                        vm.submited = false;
                        vm.isLoading=false;
                        return false;
                    }
                        var billingContactPerson = 0;
                        if (vm.Address.BillingAddress.BillingContactPerson != null) {
                            if (Array.isArray(vm.Address.BillingAddress.BillingContactPerson)) {
                                billingContactPerson = (typeof vm.Address.BillingAddress.BillingContactPerson[0] === 'object' ? vm.Address.BillingAddress.BillingContactPerson[0].value : vm.Address.BillingAddress.BillingContactPerson[0]);
                            }
                            else {
                                billingContactPerson = vm.Address.BillingAddress.BillingContactPerson.value;
                            }
                        }
                        var billingAddress = {
                            BillingContactPerson: billingContactPerson,
                            BillingAccountAddress: vm.Address.BillingAddress.BillingAccountAddress,
                            BillingAccountAddress2: vm.Address.BillingAddress.BillingAccountAddress2,
                            BillingCity: vm.Address.BillingAddress.BillingCity,
                            BillingZipCode: vm.Address.BillingAddress.BillingZipCode,
                            BillingState: vm.Address.BillingAddress.BillingState,
                            BillingCountry: vm.Address.BillingAddress.BillingCountry,
                            BillingAccountCity: vm.Address.BillingAddress.BillingAccountCity
                        };
                        var shippingContactPerson = 0;
                        if (vm.Address.ShippingAddress.ShippingContactPerson != null) {
                            if (Array.isArray(vm.Address.ShippingAddress.ShippingContactPerson)) {
                                shippingContactPerson = (typeof vm.Address.ShippingAddress.ShippingContactPerson[0] === 'object' ? vm.Address.ShippingAddress.ShippingContactPerson[0].value : vm.Address.ShippingAddress.ShippingContactPerson[0]);
                            }
                            else {
                                shippingContactPerson = vm.Address.ShippingAddress.ShippingContactPerson.value;
                            }
                        }
                        var shippingAddress = {
                            ShippingContactPerson: shippingContactPerson,
                            ShippingAccountAddress: vm.Address.ShippingAddress.ShippingAccountAddress,
                            ShippingAccountAddress2: vm.Address.ShippingAddress.ShippingAccountAddress2,
                            ShippingCity: vm.Address.ShippingAddress.ShippingCity,
                            ShippingZipCode: vm.Address.ShippingAddress.ShippingZipCode,
                            ShippingState: vm.Address.ShippingAddress.ShippingState,
                            ShippingCountry: vm.Address.ShippingAddress.ShippingCountry,
                            ShippingAccountCity: vm.Address.ShippingAddress.ShippingAccountCity
                        };
                        var address = {
                            BillingAddress: billingAddress,
                            ShippingAddress: shippingAddress
                        }
                        debugger
                        var status = {
                            Status: statusType
                        }
                        var subModuleCode = {
                            SubModuleCode: vm.$route.params.subModuleCode,
                        }
                        var postJSON = {
                            data: data,
                            address: address,
                            productSummary: productSummary,
                            validation: validation,
                            status: status,
                            subModuleCode: subModuleCode
                        };
                        if (chkSummaryData.length == 0) {
                            var objectPostString = JSON.stringify(postJSON);
                            LeadDataService.SaveOrder(objectPostString).then(response => {
                                if (response.data[0].Code == "1") {
                                    var msg = "";
                                    vm.isLoading=false;
                                    if (vm.salesorderID > 0) {
                                        if (statusType == "D") {
                                           vm.ShowAlert(vm.$t('DraftSuccess',[vm.$t('Sales_Order')]),"success", true, vm.$t("Alert"));
                                        }
                                        else {
                                           vm.ShowAlert(vm.$t("UpdatedSuccess",[vm.$t('Sales_Order')]),"success", true, vm.$t("Alert"));
                                        }                                       
                                    }
                                    else {
                                        if (statusType == "D") {
                                            vm.ShowAlert(vm.$t('DraftSuccess',[vm.$t('Sales_Order')]),"success", true, vm.$t("Alert"));
                                        }
                                        else {
                                            // vm.ShowAlert(vm.$t("SubmitSuccess",[vm.$t('Sales_Order')]),vm.$t("Generated"),response.data[0].Name,"success", true, vm.$t("Alert"));                    
                                            msg = vm.$t("SubmitSuccess",[vm.$t('Sales_Order'),vm.$t("Generated").toLowerCase(),response.data[0].Name]);
                                       
                                              vm.ShowAlert(msg, "success", true, vm.$t("Alert"));
                                        } 
                                    }
                                    if(vm.$route.params.reqFromId!="0"){
                                        vm.$router.push({path:`/CRM/ViewDealDetails/${vm.$route.params.reqFromId}/CRM/CRM_DEALS`});
                                    }else{
                                        vm.$router.push({ name: 'CRMSalesOrderListing' });
                                    }
                                }
                                else {                                    
                                    vm.ShowAlert(vm.$t('AddedError', [vm.$t('Sales_Order')]), "failure", true, vm.$t("Alert"));
                                }
                                vm.isLoading=false;                               
                                vm.submited = false;
                            });
                        }
                        else {
                            vm.isLoading=false;  
                            vm.submited = false;                            
                            vm.ShowAlert(vm.$t("PleaseAddSomeLineItems"), "warning", true, vm.$t("Alert"));
                        }
                    }
                    else {    
                        vm.isLoading=false;  
                            vm.submited = false;                     
                        vm.ShowAlert(vm.$t("CorrectHighlightedErrors"), "warning", true, vm.$t("Alert"));
                    }
                });
        },            
        AddNewProductSummary: function () {
            var productSummary = {
                ProductName: null,
                ProductList: [],
                ProductDescription: '',
                ProductQuantity: 0,
                ProductInStock: 0,
                ProductRate: 0,
                ProductTax: 0,
                ProductAmount: 0,
                ProductDiscount: 0,
                ProductDiscountType: 'percent',
                ProductDiscountValue: 0,
                ProductItemTotal: 0,
                PriceBookId: 0,
                toggleDiscount: false,
                toggleTax: false,
                TaxData: [],
                DiscountData: []
            };
            this.ProductSummary.SummaryData.push(productSummary);
        },
        DeleteProductSummaryItem: function (item, index) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("ProductSummary")), function () {
                vm.ProductSummary.SummaryData.splice(index, 1);
            });
        },
        FillDropdown: function (type, refId) {           
                var vm=this;
                var search = '';
                var typeCode = type;
                if (type == "BillingZipCode" || type == "ShippingZipCode") {
                    typeCode = 'DISTRICT_ZIPCODE';
                }
                else if (type == "BillingCity" || type == "ShippingCity") {
                    typeCode = 'STATE_DISTRICT';
                }
                else if (type == "BillingState" || type == "ShippingState") {
                    typeCode = 'STATE';
                }
                if (refId != '') {
                    var url = `id=&moduleName=${vm.$route.params.moduleName}&type=${typeCode}&refId=${refId}`;                    
                    LeadDataService.getDDLData(url).then(response => {                        
                                var json = [];
                                if (response.data) {
                                    json = response.data.DATA;
                                }
                                if (json != null) {                                    
                                    if (type == "BillingZipCode") {
                                        vm.Address.BillingAddress.BillingZipCode = '';
                                        vm.Address.BillingAddress.BillingZipCodeList = [];
                                        vm.Address.BillingAddress.BillingZipCodeList = json;
                                    }
                                    else if (type == "BillingCity") {
                                        vm.Address.BillingAddress.BillingCity = '';
                                        vm.Address.BillingAddress.BillingZipCode = '';
                                        vm.Address.BillingAddress.BillingZipCodeList = [];
                                        vm.Address.BillingAddress.BillingCityList = json;
                                    }
                                    else if (type == "BillingState") {
                                        vm.Address.BillingAddress.BillingStateList = json;
                                        vm.Address.BillingAddress.BillingCity = '';
                                        vm.Address.BillingAddress.BillingZipCode = '';
                                        vm.Address.BillingAddress.BillingState = '';
                                        vm.Address.BillingAddress.BillingCityList = [];
                                        vm.Address.BillingAddress.BillingZipCodeList = [];
                                    }
                                    else if (type == "ShippingZipCode") {
                                        vm.Address.ShippingAddress.ShippingZipCode = '';
                                        vm.Address.ShippingAddress.ShippingZipCodeList = [];
                                        vm.Address.ShippingAddress.ShippingZipCodeList = json;
                                    }
                                    else if (type == "ShippingCity") {
                                        vm.Address.ShippingAddress.ShippingCity = '';
                                        vm.Address.ShippingAddress.ShippingZipCode = '';
                                        vm.Address.ShippingAddress.ShippingZipCodeList = [];
                                        vm.Address.ShippingAddress.ShippingCityList = json;
                                    }
                                    else if (type == "ShippingState") {
                                        vm.Address.ShippingAddress.ShippingStateList = json;
                                        vm.Address.ShippingAddress.ShippingCity = '';
                                        vm.Address.ShippingAddress.ShippingZipCode = '';
                                        vm.Address.ShippingAddress.ShippingState = '';
                                        vm.Address.ShippingAddress.ShippingCityList = [];
                                        vm.Address.ShippingAddress.ShippingZipCodeList = [];
                                    }
                                    else if (type == "ACCOUNT_CONTACTS") {
                                        debugger;
                                        vm.ShippingContactPerson = json;
                                        $.each(vm.LeadData, function (filedIndex, fieldItem) {
                                            if (fieldItem.name == 'contact_id') {
                                                fieldItem.select_options = json;
                                                if (ajaxRequestComplete) {
                                                    fieldItem.value = '';
                                                    fieldItem.select_value = '';
                                                    vm.Address.ShippingAddress.ShippingContactPerson = '';
                                                    vm.Address.BillingAddress.BillingContactPerson = '';
                                                }
                                            }
                                        });
                                    }
                                    else if (type == "ACCOUNT_DEALS") {
                                        $.each(vm.LeadData, function (filedIndex, fieldItem) {
                                            if (fieldItem.name == 'deal_id') {
                                                fieldItem.select_options = json;
                                            }
                                        });
                                    }
                                }
                                
                            });
                    
                }
                else {
                    if (type == "BillingZipCode") {
                        vm.Address.BillingAddress.BillingZipCode = '';
