        SelectPriceBook: function (item, index) {
                this.selectedItemIndex = index;
                this.showPricebookModal = true;
        },
        CancelPriceBook: function (item, index) {
            if (Array.isArray(item.ProductName)) {
                if (item.ProductName[0].second_value > 0) {
                    item.PriceBookId = 0;
                    item.ProductRate = item.ProductName[0].second_value;
                    item.ProductDiscountType = 'percent',
                        item.ProductDiscountValue = 0;
                }
            }
            else {
                if (item.ProductName.second_value > 0) {
                    item.PriceBookId = 0;
                    item.ProductRate = item.ProductName.second_value;
                    item.ProductDiscountType = 'percent',
                        item.ProductDiscountValue = 0;
                }
                else if (item.ProductName.first_value > 0) {
                    item.PriceBookId = 0;
                    item.ProductRate = item.ProductName.second_value;
                    item.ProductDiscountType = 'percent',
                        item.ProductDiscountValue = 0;
                }
                else if (item.ProductName.value > 0) {
                    item.PriceBookId = 0;
                    item.ProductRate = item.ProductName.second_value;
                    item.ProductDiscountType = 'percent',
                        item.ProductDiscountValue = 0;
                }
            }
            this.CalculateAmount(item);
            this.totalSumlineItem(item);
        },
        CalculateAmount: function (item) {
            if (item.ProductQuantity > 0 && item.ProductRate > 0) {
                item.ProductAmount = parseFloat(item.ProductQuantity, 10) * parseFloat(item.ProductRate, 10);
                this.CalculateDiscount(item);
            }
        },
        CheckQuantity: function (item, event) {
            var vm=this;
                var itemId = 0;
                if (item.ProductName != null) {
                    if (Array.isArray(item.ProductName)) {
                        itemId = item.ProductName[0];
                    }
                    else {
                        itemId = item.ProductName.value;
                    }
                }
                if (event.target.value > 0 && itemId > 0) {
                    $(event.target).addClass("loadinginner");
                    LeadDataService.CheckItemQuantity("itemId="+itemId).then(response => {
                             var json = response.data;
                             $(event.target).removeClass("loadinginner");
                             if (json != null) {
                                 if (typeof (item.SelectedQuantityJustForMatch) === 'undefined') {
                                     item.ProductInStock = json.DATA;
                                 } else {
                                     item.ProductInStock = (parseInt(json.DATA) + parseInt(item.SelectedQuantityJustForMatch));
                                 }
                             }
                             if (item.ProductInStock < event.target.value) {
                                 item.ProductQuantity = item.ProductInStock;
                             }
                             vm.CalculateAmount(item);
                             if (item.ProductInStock <= 0) {
                                 item.ProductQuantity = 0;
                             }
                         });
                }
        },
        CalculateDiscount: function (item) {
            var chkItem = $.grep(item.DiscountData, function (element, index) {
                return element.FROM_RANGE <= item.ProductQuantity && element.TO_RANGE >= item.ProductQuantity;
            });
            if (chkItem != null && chkItem.length > 0) {
                //item.ProductDiscountType = 'percent',
                item.ProductDiscountValue = parseFloat(chkItem[0].DISCOUNT, 10).toFixed2();
            }
        },
        ShowDiscount: function (item) {
            let vm=this;
             setTimeout(function () {vm.RadioButtonBootstrap()}, 500);
            item.toggleDiscount = !item.toggleDiscount;
        },
        CalculateTax: function () {
            var data = this.ProductSummary.SummaryData;
            var vm = this;
            $.each(data, $.proxy(function (key, item) {
                vm.ShowTaxItems(item);
            }, this));
        },
        ShowTaxItems: function (item) {
                var state = this.Address.ShippingAddress.ShippingState;
                var countryId = this.Address.ShippingAddress.ShippingCountry;
                var zipcode = this.Address.ShippingAddress.ShippingZipCode;
                var district = this.Address.ShippingAddress.ShippingCity;
                var projectId = 0;
                if (item.ProductName != null) {
                    if (Array.isArray(item.ProductName)) {
                        projectId = item.ProductName[0];
                    }
                    else {
                        projectId = item.ProductName.value;
                    }
                }
                //item.TaxData = []; commented by vijender on 2021-10-01 because when we edit then data not come.
                if (projectId > 0 && countryId > 0) {
                    
                    var url =`state=${state}&zipcode=${zipcode}&district=${district}&countryId=${countryId}&projectId=${projectId}`;
                   LeadDataService.GetTaxByProduct(url).then(response => {
                             var json = response.data;
                             
                             if (json != null && json != "") {
                                 item.TaxData = [];
                                 item.TaxData = json.DATA;
                                 return item.TaxData;
                             }
                         });
                }
        },
        ShowTax: function (item) {
                item.toggleTax = !item.toggleTax;
                //ShowTaxItems(item);
        },
        AddMoreTax: function (item) {
            var objTax = {
                TAX_ID: 0,
                TAX_FOR: 'custom',
                TAX_TYPE: 'percent',
                TAX_RATE: 0,
                TAX_NAME: ''
            }
            item.TaxData.push(objTax);
        },
        DeleteMoreTax: function (item, taxItem) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("Tax")), function () {
                item.TaxData.splice(taxItem, 1);
                item.ProductTax = '0.00';
            });
        },
