                               if(vm.salesorderID=="0")
                                vm.IsInventory = response.data.INVENTORY_APPLICABLE[0].INVENTORY_APPLICABLE == "no" ? false : true;
                              if (vm.LeadData != null && vm.LeadData.length > 0) {
                                  var groupId = vm.LeadData[0].group_id;
                                  $.each(vm.LeadData, $.proxy(function (index, item) {
                                      groupId = item.group_id;
                                      if (item.dt_code == 'date') {
                                        item.value= item.value==''?null:item.value;
                                        }
                                     
                                      if (item.dt_code == 'checkbox') {
                                          item.select_value = item.value.split(',');
                                      }
                                      if (item.name == "contact_id" && item.select_options && vm.salesorderID > 0) {
                                          var newSelect = item.select_options.reduce(function (memo, e1) {
                                              var matches = memo.filter(function (e2) {
                                                  return e1.name == e2.name && e1.value == e2.value
                                              })
                                              if (matches.length == 0)
                                                  memo.push(e1)
                                              return memo;
                                          }, []);
