                                        item.value =new Date();
                                        vm.DependentDateObject(item, item.value);
                                      } 
                                      if (item.name == "account_id" && vm.salesorderID == "0") {
                                        AccountD = item;
                                      }
                                      if (item.name == "deal_id" && vm.salesorderID == "0") {
                                            DealD = item;
                                        }
                                      if (item.name == 'template_id' && item.value == '') {
                                          item.value = JSON.parse(item.select_options)[0].value;
                                      }
                                      var chkGroup = $.grep(vm.GroupBy, function (element, index) {
                                          return element.group_id == groupId;
                                      });
                                      if (chkGroup.length == 0) {
                                          var returnedData = $.grep(vm.LeadData, function (element, index) {
                                              return element.group_id == groupId;
                                          });
