                                            vm.Address.BillingAddress.BillingCountry = billAddress==null?null:billAddress.account_country;
                                            vm.Address.BillingAddress.BillingState =billAddress==null?null: billAddress.account_state;
                                            vm.accntid =billAddress==null?null: billAddress.accid;
                                            vm.IsInventory = productSummaryList[0].INVENTORY_APPLICABLE == "no" ? false : true;
                                            $.each(productSummaryList, $.proxy(function (index, item) {
                                                var obj = {
                                                    ProductName: vm.MakeNormalArray(item.product_name),
                                                    ProductList: [],
                                                    ProductDescription: item.item_product_desc,
                                                    ProductQuantity: item.item_product_Quantity,
                                                    ProductInStock: (parseInt(item.inventory_available_quantity)),//+ parseInt(item.item_product_Quantity)),
                                                    ProductRate: item.item_product_list_price,
                                                    ProductTax: 0,
                                                    ProductAmount: item.item_product_amount,
                                                    ProductDiscount: item.item_product_discount,
                                                    ProductDiscountType: item.item_product_discount_type,
                                                    ProductDiscountValue: item.item_product_discount_value,
                                                    ProductItemTotal: item.item_total,
                                                    PriceBookId: item.item_price_book_id,
                                                    toggleDiscount: false,
                                                    toggleTax: false,
                                                    TaxData: (item.tax_data != null) ? vm.MakeNormalArray(item.tax_data) : [],
                                                    DiscountData: [],
                                                };
                                                productSummary.push(obj);
                                            }, vm));
                                        }
                                        vm.ProductSummary = {
                                            SummaryData: productSummary
                                        }
                                    });
                                }
                            }
                              //ProductSummary
                            setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                            setTimeout(function () {vm.RadioButtonBootstrap()}, 500);
                            // setTimeout(function () {vm.ShowPopOver()}, 500);
                            if(vm.salesorderID>0){
                                var frmFields = {}
                                vm.LeadData.forEach((item) => {
                                    if (item.astype != '' && item.form_field_visibility == 'YES') {
                                        frmFields[item.name] = item.value;
                                    }
                                })
                                if (vm.ProductSummary.SummaryData.length > 0) {
                                    vm.ProductSummary.SummaryData.forEach((item, index) => {
                                        if (item.ProductQuantity) {
                                            frmFields['Quantity' + index] = item.ProductQuantity;
                                        }
                                    })
                                }
                                // vm.$refs.form.resetForm({ values: frmFields });
                             }
                        });
        },
        totalSumlineItem: function (item) {
                var productAmount = 0;
                var productDiscount = 0;
                var productTax = 0;
                if (item.ProductAmount > 0) {
                    productAmount = parseFloat(item.ProductAmount, 10);
                }
                productAmount = parseFloat(productAmount, 10);
                if (item.ProductDiscountType == 'percent') {
                    var discountTypeValue = ($.trim(item.ProductDiscountValue) != '') ? $.trim(item.ProductDiscountValue) : 0;
                    if (discountTypeValue > 0) {
                        var c = (parseFloat(productAmount, 10) * parseFloat(discountTypeValue, 10)) / 100;
                        item.ProductDiscount = parseFloat(c, 10).toFixed2();
                    }
                    else {
                        item.ProductDiscount = 0;
                    }
                }
                else {
                    item.ProductDiscount = parseFloat((($.trim(item.ProductDiscountValue) != '') ? $.trim(item.ProductDiscountValue) : 0), 10).toFixed2();
                }
                if (item.ProductDiscount > 0) {
                    productDiscount = parseFloat(item.ProductDiscount, 10);
                    productAmount = parseFloat(productAmount, 10) - parseFloat(item.ProductDiscount, 10);
                }
                if (item.TaxData != null && item.TaxData.length > 0) {
                    var totalTax = 0;
                    $.each(item.TaxData, $.proxy(function (index, taxItem) {
                        var taxrate = parseFloat(taxItem.TAX_RATE, 10);
                        if (taxItem.TAX_TYPE == "percent") {
                            if (taxrate > 0) {
                                var c = (parseFloat(productAmount, 10) * parseFloat(taxrate, 10)) / 100;
                                totalTax += parseFloat(c, 10);
                            }
                        }
                        else {
                            totalTax += parseFloat(taxrate, 10);
                        }
                    }, this));
                    if (totalTax > 0) {
                        totalTax = parseFloat(totalTax, 10);
                        productAmount = parseFloat(productAmount, 10) + parseFloat(totalTax, 10);
                        item.ProductTax = totalTax.toFixed2();
                    } else {
                        item.ProductTax = 0.00;
                    }
                }
                var total = 0;
                total = (parseFloat(item.ProductAmount, 10) + parseFloat(item.ProductTax, 10)) - (parseFloat(item.ProductDiscount, 10));
                item.ProductItemTotal = parseFloat(total, 10).toFixed2();
                return total.toFixed2();
        },
        SetPriceBook: function (item) {
            var index = this.selectedItemIndex;
                this.ProductSummary.SummaryData[index].ProductRate = item.LIST_PRICE;
                this.ProductSummary.SummaryData[index].PriceBookId = item.PRICE_BOOK_ID;
                this.ProductSummary.SummaryData[index].DiscountData = this.MakeNormalArray(item.PRICE_BOOK_RANGE);
                this.ProductSummary.SummaryData[index].ProductDiscountType = ((item.PRICE_BOOK_MODEL == 2) ? 'percent' : 'flat');
                this.showPricebookModal = false;
                this.CalculateAmount(this.ProductSummary.SummaryData[index]);
        }
    },
    computed: {
            summaryTotal: function () {
                let total = 0;
                var data = this.ProductSummary.SummaryData;
                for (let i = 0; i < data.length; i++) {
                    if (data[i].ProductRate > 0) {
                        total = parseFloat(total, 10) + parseFloat(data[i].ProductItemTotal, 10);
                    }
                }
                total = parseFloat(total, 10).toFixed2();
                this.ProductSummary.SubTotal = total;
                if (this.ProductSummary.ShippingCharges > 0) {
                    total = parseFloat(total, 10) + parseFloat(this.ProductSummary.ShippingCharges, 10);
                }
                if (this.ProductSummary.Adjustments > 0) {
                    total = parseFloat(total, 10) - parseFloat(this.ProductSummary.Adjustments, 10);
                }
                total = parseFloat(total, 10).toFixed2();
                this.ProductSummary.SummaryTotal = total;
                return total;
            },
    }
}
</script>

