                                                <td class="position-relative overflow-visible single-action">
                                                    <span>{{item.ProductTax }}</span>                                                   
                                                    <a href="javascript:;" class="d-inline-block" :title="$t('Add')"
                                                        v-if="item.ProductName != '' && item.ProductName != null"
                                                        v-on:click="ShowTax(item)"><em class="fa fa-arrow-circle-right text-info"
                                                            style="font-size:20px;"></em></a>
                                                    <div class="position-absolute panelcoming paneltax panelfix bg-light listing border-0 p-0 bg-white"
                                                        v-if='item.toggleTax' v-click-outside="outside">
                                                        <div class="col-12 float-left p-0">
                                                            <div class="p-0 border-0"><a href="javascript:;" class="close pt-0"
                                                                    style="font-size:23px; right: -20px; top: 10px;"
                                                                    data-dismiss="alert" v-on:click="ShowTax(item)">&times;</a>
                                                            </div>
                                                            <div class="table-responsive">
