                                                                                {{$t('NoRecordfound')}}</td>
                                                                        </tr>
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td><span>{{totalSumlineItem(item)}}</span></td>
                                                <td class="text-center single-action" data-width="50">
                                                    <a href="javascript:;" v-on:click="DeleteProductSummaryItem(item, index)"><em                        class="fa fa-trash action-icon text-danger"></em></a>
                                                </td>
                                            </tr>
                                            <tr>
