                                            </tr>
                                            <tr>
                                                <td colspan="7"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('Adjustments')}} :</td>
                                                <td colspan="2" class="border-bottom-0"><input class="form-control" type="text"
                                                        placeholder="0.00" v-model="ProductSummary.Adjustments"></td>
                                            </tr>
                                           
                                            <tr>
                                                <td colspan="7"
