                                                    {{$t('Total')}} :</td>
                                                <td colspan="2" class="border-bottom-0"><span
                                                        v-bind:value="summaryTotal">{{ProductSummary.SummaryTotal}}</span></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
        
        
                        <div class="row flex-row-reverse mt-2">
